html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  line-height: 1.5;
  height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(1.15rem + 0.1vw);
}

h2 {
  font-size: calc(1.15rem + 0.2vw);
}

a {
  text-decoration: none;
  color: #212121;
}

button {
  background-color: transparent;
  padding: 0.5rem 1rem;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

footer {
  margin-top: auto;
  text-align: center;
  padding: 1rem auto;
}

select {
  border: none;
}

.display-block {
  display: block;
}

.display-none {
  display: none !important;
}

nav {
  font-weight: bold;
  text-transform: uppercase;
  border-top: 1px solid #e0e0e0;
  border-bottom: 1px solid #e0e0e0;
  padding: 0 !important;
}
nav div {
  margin: 0 8rem;
}

.navbar-nav {
  text-align: center;
}

.language {
  text-align: center;
  margin: 2rem;
}

select:focus {
  outline: none;
}
select option {
  height: 2rem;
}

@media (min-width: 992px) {
  .navbar-brand {
    display: none;
    text-align: left;
  }

  .navbar-nav > * {
    margin: auto 2rem;
  }

  nav {
    font-size: 1.25rem;
  }
  nav .language {
    border-left: 1px solid #e0e0e0;
    border-right: 1px solid #e0e0e0;
    height: 5rem;
    display: flex;
    align-items: center;
    margin: 0;
    text-align: left;
    margin-right: 0;
  }
  nav .language select {
    margin: 1rem;
    height: 5rem;
  }
}
.counter-section {
  background-image: url("/build/img/general/counter-bg.webp");
  background-position: top;
}

.counter-title {
  position: relative;
  padding: 4rem 3rem;
}
.counter-title h2 {
  font-size: calc(1.25rem + 0.6vw);
  text-align: center;
}
.counter-title p {
  font-size: calc(1.25rem + 0.3vw);
  text-align: center;
}
@media (min-width: 576px) {
  .counter-title {
    margin-top: 4rem;
  }
}
@media (min-width: 768px) {
  .counter-title {
    margin: 10rem 20%;
  }
  .counter-title h2 {
    font-size: calc(1.25rem + 1.25vw);
  }
  .counter-title p {
    padding: 2rem;
    font-size: calc(1.25rem + 0.5vw);
  }
}

.wrapper {
  background-color: #469ed5a3;
  display: flex;
  justify-content: space-around;
  position: relative;
}

.container {
  width: 28vmin;
  height: 28vmin;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 2.5rem 0;
  position: relative;
}
@media (min-width: 320px) {
  .container {
    position: relative;
    top: 10px;
  }
}
@media (min-width: 576px) {
  .container {
    top: -20px;
  }
}
@media (min-width: 768px) {
  .container {
    top: -30px;
  }
}

span.num {
  display: grid;
  place-items: center;
  font-weight: 600;
  font-size: 3em;
}

span.text {
  font-size: 1em;
  text-align: center;
  font-weight: 300;
  line-height: 0;
}

@media screen and (max-width: 1200px) {
  .wrapper {
    width: 100vw;
  }

  .container {
    height: 26vmin;
    width: 26vmin;
    font-size: 12px;
  }
}
@media screen and (max-width: 768px) {
  .wrapper {
    width: 100%;
    flex-wrap: wrap;
    gap: 30px;
  }

  .container {
    width: calc(50% - 40px);
    height: 30vmin;
    font-size: 14px;
  }
}
@media screen and (max-width: 576px) {
  .wrapper {
    gap: 15px;
    padding-bottom: 5rem;
  }

  .container {
    width: 100%;
    height: 25vmin;
    font-size: 8px;
  }
}
.counter-section {
  color: #ffffff;
}

.header-info {
  display: none;
  margin: 1rem 10rem;
  font-size: 1.1rem;
}

.header-logo {
  height: 5rem;
}
.header-logo img {
  height: 100%;
}

.navbar {
  display: flex;
  flex-direction: column;
}
.navbar .navbar-brand {
  margin: 1rem;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-content: center;
}
.navbar .navbar-brand img {
  width: 55vw;
  max-width: 15rem;
  align-items: center;
}
@media (min-width: 992px) {
  .navbar .navbar-brand {
    display: none;
  }
}
.navbar .navbar-toggler {
  margin-bottom: 2rem;
  margin-left: 4rem;
  margin-right: 4rem;
  padding: 0.75rem 0.75rem;
}
@media (min-width: 320px) {
  .navbar {
    flex-direction: row;
  }
  .navbar .navbar-toggler {
    margin-bottom: unset;
    padding: 0.75rem 1rem;
  }
}

/* Style all font awesome icons */
.fa {
  color: #ffffff;
  background-color: #469DD5;
  padding: 6px;
  font-size: 9px;
  width: 21px;
  border-radius: 50%;
  text-decoration: none;
  margin: 5px 2px;
  text-align: center;
  display: inline-block;
  text-rendering: auto;
}

.fa-facebook:before {
  position: relative;
  top: 1px;
  right: 1px;
}

.fa-instagram:before {
  position: relative;
  top: 1px;
  right: 0px;
}

/* Add a hover effect if you want */
.fa:hover {
  background-color: #70b3de;
  text-decoration: none;
  transition: all 0.1s linear;
  color: #ffffff;
}

@media (min-width: 992px) {
  .header-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header-links {
    display: flex;
    justify-content: center;
  }
  .header-links > * {
    padding-right: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header-links > * i {
    margin-right: 2rem;
    color: #469DD5;
  }

  .header-info-text {
    display: flex;
    flex-direction: column;
  }
  .header-info-text p {
    margin: 0;
  }
  .header-info-text p:last-of-type {
    color: #a1a1a1;
  }
}
.flex-column-row {
  display: flex;
  flex-direction: column;
  margin: 2rem;
}
.flex-column-row > * {
  padding: 3rem;
  border-top: 1px solid #e0e0e0;
  border-right: 1px solid #e0e0e0;
  border-left: 1px solid #e0e0e0;
}
.flex-column-row > *:last-of-type {
  border-bottom: 1px solid #e0e0e0;
}
.flex-column-row > * h2 {
  color: #469DD5;
  text-transform: uppercase;
}
.flex-column-row > * h2 span {
  margin-top: 1rem;
  margin-bottom: 2rem;
  color: #212121;
  display: block;
}

@media (min-width: 576px) {
  .flex-column-row {
    flex-direction: row;
  }
  .flex-column-row > * {
    padding: 4rem;
    border-right: none;
    border-bottom: 1px solid #e0e0e0;
  }
  .flex-column-row > *:last-of-type {
    border-right: 1px solid #e0e0e0;
  }
}
@media (min-width: 768px) {
  .flex-column-row {
    position: relative;
    top: -5rem;
    background-color: #ffffff;
    margin-bottom: 0;
  }
}
@media (min-width: 992px) {
  .flex-column-row {
    width: 85vw;
    margin-left: auto;
    margin-right: auto;
  }
}
.grid-section {
  margin: 4rem;
  margin-top: 0;
}
.grid-section .section-title {
  text-align: center;
}
.grid-section .section-title h2 {
  font-size: calc(1.25rem + 0.6vw);
  margin: 2rem 2rem 4rem 2rem;
  text-transform: uppercase;
}
.grid-section .section-title p {
  font-size: calc(1.25rem + 0.3vw);
}
.grid-section .grid-1-2-3 {
  display: grid;
  gap: 4rem;
  margin-top: 4rem;
  margin-bottom: 2rem;
}
.grid-section .grid-1-2-3 .grid-element {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.grid-section .grid-1-2-3 .grid-element img {
  width: 25%;
  margin: auto;
  padding-bottom: 4rem;
}
.grid-section .grid-1-2-3 .grid-element h3 {
  text-align: center;
}

@media (min-width: 576px) {
  .grid-1-2-3 {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 768px) {
  .grid-1-2-3 {
    grid-template-columns: repeat(3, 1fr);
  }

  .grid-section .section-title h2 {
    margin: 4rem 2rem 2rem 2rem;
    margin-top: 0;
  }
}
@media (min-width: 992px) {
  .grid-section {
    margin-top: 3rem;
    margin-bottom: 7rem;
    width: 85vw;
    margin-left: auto;
    margin-right: auto;
  }
  .grid-section .grid-1-2-3 .grid-element img {
    width: 7rem;
  }
}
.grid-collaborations {
  margin-top: 2rem;
}
.grid-collaborations .section-title h2 {
  color: #469DD5;
  margin-bottom: 1rem;
}

.grid-1-2-4 .grid-element {
  margin: 4rem;
  height: 9rem;
}
.grid-1-2-4 .grid-element img {
  width: auto;
  height: 100%;
}

@media (min-width: 320px) {
  .grid-1-2-4 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 4rem;
  }
  .grid-1-2-4 .grid-element {
    margin: 1rem;
    margin-bottom: 4rem;
    display: flex;
    justify-content: center;
  }
  .grid-1-2-4 .grid-element img {
    height: 100%;
    width: auto;
  }
}
@media (min-width: 576px) {
  .grid-1-2-4 {
    width: 70%;
    margin: inherit auto;
  }
}
@media (min-width: 768px) {
  .grid-1-2-4 {
    grid-template-columns: repeat(4, 1fr);
    width: 90%;
    margin-top: 5rem;
  }
}
